___CSS_LOADER_EXPORT___.push([module.id, "\n#formSigner{\n\twidth: 60%;\n\ttext-align: left;\n\tmargin: 20px;\n}\n.form-group > input[type='text'] {\n\twidth: 100%;\n}\n\n", "",{"version":3,"sources":["webpack://src/AdminFormSigner.vue"],"names":[],"mappings":";AA6IA;CACA,UAAA;CACA,gBAAA;CACA,YAAA;AACA;AAEA;CACA,WAAA;AACA","sourcesContent":["<template>\n\t<div id=\"formSigner\" class=\"form-signer\">\n\t\t<div class=\"form-group\">\n\t\t\t<label for=\"commonName\">{{ t('signer', 'Nome (CN)') }}</label>\n\t\t\t<input\n\t\t\t\tid=\"commonName\"\n\t\t\t\tref=\"commonName\"\n\t\t\t\tv-model=\"certificate.commonName\"\n\t\t\t\ttype=\"text\"\n\t\t\t\t:disabled=\"formDisabled\">\n\t\t</div>\n\t\t<div class=\"form-group\">\n\t\t\t<label for=\"country\">{{ t('signer', 'Pas (C)') }}</label>\n\t\t\t<input\n\t\t\t\tid=\"country\"\n\t\t\t\tref=\"country\"\n\t\t\t\tv-model=\"certificate.country\"\n\t\t\t\ttype=\"text\"\n\t\t\t\t:disabled=\"formDisabled\">\n\t\t</div>\n\t\t<div class=\"form-group\">\n\t\t\t<label for=\"organization\">{{ t('signer', 'Organizao (O)') }}</label>\n\t\t\t<input\n\t\t\t\tid=\"organization\"\n\t\t\t\tref=\"organization\"\n\t\t\t\tv-model=\"certificate.organization\"\n\t\t\t\ttype=\"text\"\n\t\t\t\t:disabled=\"formDisabled\">\n\t\t</div>\n\t\t<div class=\"form-group\">\n\t\t\t<label for=\"organizationUnit\">{{ t('signer', 'Unidade da organizao (OU)') }}</label>\n\t\t\t<input\n\t\t\t\tid=\"organizationUnit\"\n\t\t\t\tref=\"organizationUnit\"\n\t\t\t\tv-model=\"certificate.organizationUnit\"\n\t\t\t\ttype=\"text\"\n\t\t\t\t:disabled=\"formDisabled\">\n\t\t</div>\n\t\t<input\n\t\t\ttype=\"button\"\n\t\t\tclass=\"primary\"\n\t\t\t:value=\"submitLabel\"\n\t\t\t:disabled=\"formDisabled || !savePossible\"\n\t\t\t@click=\"generateCertificate\">\n\t</div>\n</template>\n\n<script>\nimport '@nextcloud/dialogs/styles/toast.scss'\nimport { generateUrl } from '@nextcloud/router'\nimport axios from '@nextcloud/axios'\nimport { showError } from '@nextcloud/dialogs'\nimport { translate as t } from '@nextcloud/l10n'\n\nexport default {\n\tname: 'AdminFormSigner',\n\tcomponents: {\n\t},\n\tdata() {\n\t\treturn {\n\t\t\tcertificate: {\n\t\t\t\tcommonName: '',\n\t\t\t\tcountry: '',\n\t\t\t\torganization: '',\n\t\t\t\torganizationUnit: '',\n\t\t\t},\n\t\t\tsubmitLabel: t('signer', 'Gerar Certificado Raiz'),\n\t\t\tformDisabled: false,\n\t\t\tloading: true,\n\t\t}\n\t},\n\tcomputed: {\n\t\tsavePossible() {\n\t\t\treturn (\n\t\t\t\tthis.certificate\n                && this.certificate.commonName !== ''\n                && this.certificate.country !== ''\n                && this.certificate.organization !== ''\n                && this.certificate.organizationUnit !== ''\n\t\t\t)\n\t\t},\n\t},\n\tasync mounted() {\n\t\tthis.loading = false\n\t\tthis.loadRootCertificate()\n\t},\n\n\tmethods: {\n\t\tasync generateCertificate() {\n\t\t\tthis.formDisabled = true\n\t\t\tthis.submitLabel = 'Gerando Certificado...'\n\t\t\ttry {\n\t\t\t\tconst response = await axios.post(\n\t\t\t\t\tgenerateUrl('/apps/signer/api/0.1/admin/certificate'),\n\t\t\t\t\tthis.certificate\n\t\t\t\t)\n\n\t\t\t\tif (!response.data || response.data.message) {\n\t\t\t\t\tthrow new Error(response.data)\n\t\t\t\t}\n\t\t\t\tthis.submitLabel = 'Certificado gerado!'\n\n\t\t\t\treturn\n\t\t\t} catch (e) {\n\t\t\t\tconsole.error(e)\n\t\t\t\tshowError(t('signer', 'No foi possivel gerar certificado'))\n\t\t\t\tthis.submitLabel = 'Gerar Certificado Raiz'\n\n\t\t\t}\n\t\t\tthis.formDisabled = false\n\t\t},\n\n\t\tasync loadRootCertificate() {\n\t\t\tthis.formDisabled = true\n\t\t\ttry {\n\t\t\t\tconst response = await axios.get(\n\t\t\t\t\tgenerateUrl('/apps/signer/api/0.1/admin/certificate'),\n\t\t\t\t)\n\t\t\t\tif (!response.data || response.data.message) {\n\t\t\t\t\tthrow new Error(response.data)\n\t\t\t\t}\n\n\t\t\t\tif (response.data.commonName\n\t\t\t\t&& response.data.country\n\t\t\t\t&& response.data.organization\n\t\t\t\t&& response.data.organizationUnit\n\t\t\t\t) {\n\t\t\t\t\tthis.certificate = response.data\n\t\t\t\t\tthis.submitLabel = 'Certificado gerado!'\n\n\t\t\t\t\treturn\n\t\t\t\t}\n\t\t\t} catch (e) {\n\t\t\t\tconsole.error(e)\n\t\t\t}\n\t\t\tthis.formDisabled = false\n\t\t},\n\t},\n}\n</script>\n<style>\n#formSigner{\n\twidth: 60%;\n\ttext-align: left;\n\tmargin: 20px;\n}\n\n.form-group > input[type='text'] {\n\twidth: 100%;\n}\n\n</style>\n"],"sourceRoot":""}]);
